import { showToast } from 'vant';
import 'vant/es/toast/style';
import { ref } from 'vue';
// import request from '../../request/request.js'

export default {
  data() {
    return {
      defaultHeadStatus: {
        chin_up_down: 1000,
        eyes_left_right: 1500,
        eyes_up_down: 1500,
        left_cheek_up_down: 1500,
        left_eyebrow_up_down: 1500,
        left_eyelid_up_down: 1500,
        left_lip_corner_push_pull: 1500,
        right_cheek_up_down: 1500,
        right_eyebrow_up_down: 1500,
        right_eyelid_up_down: 1500,
        right_lip_corner_push_pull: 1500
      },
      currentHeadStatus: {
        chin_up_down: 1000,
        eyes_left_right: 1500,
        eyes_up_down: 1500,
        left_cheek_up_down: 1500,
        left_eyebrow_up_down: 1500,
        left_eyelid_up_down: 1500,
        left_lip_corner_push_pull: 1500,
        right_cheek_up_down: 1500,
        right_eyebrow_up_down: 1500,
        right_eyelid_up_down: 1500,
        right_lip_corner_push_pull: 1500
      },
      defaultNeckStatus: {
        pitch_velocity: 0,
        yaw_angle: 0,
        yaw_max_velocity: 3.14
      },
      currentNeckStatus: {
        pitch_velocity: 0,
        yaw_angle: 0,
        yaw_max_velocity: 3.14
      }
    };
  },
  setup() {
    const active = ref(0);
    const value1 = ref(50);
    const value2 = ref(51);
    return {
      active,
      value1,
      value2
    };
  },
  methods: {
    resetStatus() {
      this.currentHeadStatus = JSON.parse(JSON.stringify(this.defaultHeadStatus));
      this.currentNeckStatus = JSON.parse(JSON.stringify(this.defaultNeckStatus));
      this.$axios.put("http://orin-desktop.local:5000/head/neck", this.currentNeckStatus).then(res => {
        console.log(res);
        showToast(res.data);
      });
      this.$axios.put("http://orin-desktop.local:5000/head/face", this.currentHeadStatus).then(res => {
        console.log(res);
        showToast(res.data);
      });
      showToast("");
    },
    onHeadStatusChange() {
      this.$axios.put("http://orin-desktop.local:5000/head/face", this.currentHeadStatus);
      // .then(res => {
      //     // showToast(res.data)
      // });
    },

    onNeckStatusChange() {
      this.$axios.put("http://orin-desktop.local:5000/head/neck", this.currentNeckStatus);
      // .then(res => {
      //     showToast(res.data)
      // });
    }
  }
};